
.upload-preview {
  padding: 0;
  margin: 0;
  width: calc(100% - 30px);
  position: absolute;
  top: 0;
  left: 15px;
  right: 15px;
}
.upload-preview-item {
  list-style-type: none;
  display: inline-block;
  width: 20%;
  padding: 16px;
  position: relative;
}
.preview-image {
  width: 100%;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
}
.preview-image:before {
  content: "";
  padding-top: 100%;
  display: block;
}
.image {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.upload-preview-item:hover .image {
  -webkit-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
  -webkit-filter: blur(8px);
          filter: blur(8px);
}
.preview-option {
  opacity: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0.9);
  text-align: center;
  padding: calc((100% - 26px) / 2) 0;
}
.reupload {
  border: none;
  outline: none;
  border-radius: 3px;
  height: 26px;
  opacity: 0;
}
.upload-preview-item:hover .reupload {
  opacity: 1;
}
.delete {
  position: absolute;
  top: 26px;
  right: 26px;
  border: none;
  outline: none;
  border-radius: 4px;
  opacity: 0;
}
.upload-preview-item:hover .delete {
  opacity: 1;
}



